<template>
  <div class="wrapper">
    <div class="main">
      <div class="title">
        <img src="../assets/honor_title.png" width="150" height="50">
      </div>
      <div class="content" v-show="answer === 'a'">
        <div style="font-size: 20px;font-weight: bold;">类型1</div>
        <div style="font-size: 20px;font-weight: bold;">你是精致魅力女神</div>
        <div>你的性格里藏着一丝不苟的魔力</div>
        <div>可以说，你是个完美主义者</div>
        <div>做事总是那么的细致不允许自己出错</div>
        <div>你不仅爱整洁还很精致有品位，颜值超高分</div>
        <div>对于工作，或是答应别人的事情</div>
        <div>你总是会力求完美地完成</div>
      </div>
      <div class="content" v-show="answer === 'b'">
        <div style="font-size: 20px;font-weight: bold;">类型2</div>
        <div style="font-size: 20px;font-weight: bold;">你是励志沉稳学霸</div>
        <div>你习惯依靠自己的力量去度过难关</div>
        <div>有着非常坚定的意志力以及比一般人都要强的耐力</div>
        <div>只要是自己树立的目标，就会义无反顾地向前冲</div>
        <div>坚定不移地坚持着自己的信念</div>
        <div>是个有着满满雄心壮志的人</div>
        <div>职场上就算受到轻视，也不会让你停下脚步</div>
      </div>
      <div class="content" v-show="answer === 'c'">
        <div style="font-size: 20px;font-weight: bold;">类型3</div>
        <div style="font-size: 20px;font-weight: bold;">你是无敌气场女王</div>
        <div>你属于自带气场的类型</div>
        <div>性格里隐藏着权威感和支配力</div>
        <div>你做事很独立，擅长运用权术来达到自己的目的</div>
        <div>很有正义感，而且聪明有智慧</div>
        <div>这样的人往往能够成为让人信服的领袖人物</div>
      </div>
      <div class="content" v-show="answer === 'd'">
        <div style="font-size: 20px;font-weight: bold;">类型4</div>
        <div style="font-size: 20px;font-weight: bold;">你是活力元气少女</div>
        <div>观察力敏锐的你，有着超强的洞察力</div>
        <div>平日的你总是元气满满</div>
        <div>这样的你，是个心思细腻的人</div>
        <div>偶尔也会想要被保护，做事也有着自己的步调和方法</div>
        <div>而你的洞察力能够让你快速的察觉到外界的变化</div>
        <div>也能让你更好地融入职场，获得认可</div>
      </div>
      <img src="../assets/stars.png" style="width: 100%;margin-top: -30px;">
    </div>
  </div>
</template>

<script>
export default {
  computed: {
    answer: function () {
      return this.$route.query.answer
    }
  }
}
</script>

<style scoped>
  .wrapper {
    width: 100%;
    height: 100%;
    background-image: url("../assets/result_bg.png");
    background-size: 100% 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .main {
    width: 75%;
    background: #ffffff;
    text-align: center;
    padding: 0 20px;
  }
  .title {
    display: flex;
    justify-content: center;
    padding-top: 40px;
  }
  .content {
    margin-top: 40px;
  }
  .content div {
    margin-bottom: 10px;
  }
</style>



