<template>
  <div class="wrapper">
    <transition name="fade">
      <div style="display: flex;justify-content: center;width: 100%;padding-top: 40px;" v-show="show">
      <img src="../assets/clear.png" style="width: 200px;height: 60px;">
      </div>
    </transition>
    <div class="next-button" @click="nextStep" v-show="showNext">点击进入迷幻测试馆</div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      index: 0,
      show: false,
      showNext: false
    }
  },
  methods: {
    nextStep () {
      let handlers = [() => {
        this.$emit('next')
        this.index++
      }]
      handlers[this.index]()
    }
  },
  mounted () {
    setTimeout(() => {
      this.show = true
      this.showNext = true
    }, 1000)
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .wrapper {
    width: 100%;
    height: 100%;
    background-image: url("../assets/honor_bg.png");
    background-size: 100% 100%;
  }
  .next-button {
    position: absolute;
    right: 20px;
    bottom: 100px;
    text-decoration: underline;
    color: #ffffff;
  }
  .fade-enter-active, .fade-leave-active {
    transition: opacity 1s;
  }
  .fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
    opacity: 0;
  }
</style>



