import Vue from 'vue'
import Router from 'vue-router'
import Wrapper from '@/components/Wrapper'
import Result from '@/components/Result'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Wrapper',
      component: Wrapper
    },
    {
      path: '/result',
      name: 'Result',
      component: Result
    }
  ]
})



