<template>
  <div class="wrapper">
  <transition name="page">
    <Home v-if="showHome" @next="HomeNext"></Home>
  </transition>
  <transition name="page">
    <Form v-if="showForm" @next="FormNext"></Form>
  </transition>
  <transition name="page">
    <Coffee v-if="showCoffee" @next="CoffeeNext"></Coffee>
  </transition>
  <transition name="page">
    <Back v-if="showBack" @next="BackNext"></Back>
  </transition>
  <transition name="page">
    <Honor v-if="showHonor" @next="HonorNext"></Honor>
  </transition>
    <transition name="page">
      <QOne v-if="showOne" @jump="jump"></QOne>
    </transition>
    <transition name="page">
      <QTwo v-if="showTwo" @jump="jump"></QTwo>
    </transition>
    <transition name="page">
      <QThree v-if="showThree" @jump="jump"></QThree>
    </transition>
    <transition name="page">
      <QFour v-if="showFour" @jump="jump"></QFour>
    </transition>
    <transition name="page">
    <QFive v-if="showFive" @jump="jump"></QFive>
  </transition>
    <transition name="page">
      <QSix v-if="showSix" @jump="jump"></QSix>
    </transition>
    <transition name="page">
      <QSeven v-if="showSeven" @jump="jump"></QSeven>
    </transition>
    <transition name="page">
      <QEight v-if="showEight" @jump="jump"></QEight>
    </transition>
  </div>
</template>

<script>
import Home from './Home.vue'
import Form from './Form.vue'
import Coffee from './Coffee.vue'
import Back from './Back.vue'
import Honor from './Honor.vue'
import QOne from './QOne.vue'
import QTwo from './QTwo.vue'
import QThree from './QThree.vue'
import QFour from './QFour.vue'
import QFive from './QFive.vue'
import QSix from './QSix.vue'
import QSeven from './QSeven.vue'
import QEight from './QEight.vue'

export default {
  components: {
    Home,
    Form,
    Coffee,
    Back,
    Honor,
    QOne,
    QTwo,
    QThree,
    QFour,
    QFive,
    QSix,
    QSeven,
    QEight
  },
  data () {
    return {
      showHome: true,
      showForm: false,
      showCoffee: false,
      showBack: false,
      showHonor: false,
      showOne: false,
      showTwo: false,
      showThree: false,
      showFour: false,
      showFive: false,
      showSix: false,
      showSeven: false,
      showEight: false
    }
  },
  methods: {
    HomeNext () {
      this.showHome = false
      this.showForm = true
    },
    FormNext () {
      this.showForm = false
      this.showCoffee = true
    },
    CoffeeNext () {
      this.showCoffee = false
      this.showBack = true
    },
    BackNext () {
      this.showBack = false
      this.showHonor = true
    },
    HonorNext () {
      this.showHonor = false
      this.showOne = true
    },
    jump (v) {
      this[v[0]] = false
      this[v[1]] = true
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.wrapper {
  width: 100%;
  height: 100%;
}
.page-enter-active, .page-leave-active {
  transition: opacity 1s;
}
.page-enter, .page-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}
</style>



