<template>
  <div class="wrapper">
    <div class="time">{{time}}</div>
    <transition name="fade">
    <img src="../assets/go_home.png" style="width: 100%;margin-top: 40px;" v-show="show">
    </transition>
    <div class="next-button" @click="nextStep" v-show="showNext">继续</div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      index: 0,
      time: '',
      show: false,
      showNext: false
    }
  },
  methods: {
    nextStep () {
      let handlers = [() => {
        this.$emit('next')
        this.index++
      }]
      handlers[this.index]()
    }
  },
  mounted () {
    setTimeout(() => {
      let s = '星期三 晚上9:00'
      let i = 0
      let tm = setInterval(() => {
        if (i < s.length) {
          this.time += s[i]
          i++
        } else {
          clearInterval(tm)
          this.show = true
          this.showNext = true
        }
      }, 200)
    }, 1000)
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .wrapper {
    width: 100%;
    height: 100%;
    background-image: url("../assets/road_bg.png");
    background-size: 100% 100%;
  }
  .next-button {
    position: absolute;
    right: 20px;
    bottom: 100px;
    text-decoration: underline;
    color: #ffffff;
  }
  .time {
    width: 100%;
    padding-top: 30px;
    font-size: 30px;
    text-align: center;
    color: #ffffff;
  }
  .fade-enter-active, .fade-leave-active {
    transition: opacity 1s;
  }
  .fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
    opacity: 0;
  }
</style>



