<template>
  <div class="wrapper">
    <div class="modal">
      <div class="modal-up" :class="{goup: open}"></div>
      <div class="modal-down" :class="{godown: open}"></div>
    </div>
    <div class="time">{{time}}</div>
    <transition name="fade">
    <div class="bottom" v-show="show">
      <div class="title">又是上班的一天……</div>
      <img src="../assets/buildings.png" style="width: 100%;">
    </div>
    </transition>
    <div class="next-button" @click="nextStep" v-show="showNext">继续</div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      showNext: false,
      index: 0,
      open: false,
      time: '',
      show: false
    }
  },
  methods: {
    nextStep () {
      let handlers = [() => {
        this.$emit('next')
        this.index++
      }]
      handlers[this.index]()
    }
  },
  watch: {
    open (e) {
      if (e) {
        setTimeout(() => {
          let s = '星期三 早上9:00'
          let i = 0
          let tm = setInterval(() => {
            if (i < s.length) {
              this.time += s[i]
              i++
            } else {
              clearInterval(tm)
              this.show = true
              this.showNext = true
            }
          }, 200)
        }, 1000)
      }
    }
  },
  mounted () {
    setTimeout(() => {
      this.open = true
    }, 1000)
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .wrapper {
    width: 100%;
    height: 100%;
  }
  .next-button {
    position: absolute;
    right: 20px;
    bottom: 100px;
    text-decoration: underline;
    z-index: 9999;
  }
  .modal {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    z-index: 9998;
  }
  .modal-up {
    width: 100%;
    height: 50%;
    background-image: url("../assets/home_up.jpeg");
    background-size: 100% 100%;
  }
  .modal-down {
    width: 100%;
    height: 50%;
    background-image: url("../assets/home_down.jpeg");
    background-size: 100% 100%;
  }
  .goup {
    transform: translateY(-100%);
    transition: transform 1s;
  }
  .godown {
    transform: translateY(100%);
    transition: transform 1s;
  }
  .time {
    width: 100%;
    padding-top: 30px;
    font-size: 30px;
    text-align: center;
  }
  .bottom {
    position: absolute;
    width: 100%;
    bottom: 0;
  }
  .title {
    width: 100%;
    margin-bottom: 100px;
    font-size: 30px;
    text-align: center;
  }
  .fade-enter-active, .fade-leave-active {
    transition: opacity 1s;
  }
  .fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
    opacity: 0;
  }
</style>



