<template>
  <div class="wrapper">
    <transition name="fade">
      <img style="width: 100%;" src="../assets/coffee_boss_1.png" v-show="shows[0]">
    </transition>
    <transition name="fade">
      <img style="width: 100%;" src="../assets/coffee_boss_2.png" v-show="shows[2]">
    </transition>
    <div class="bottom">
      <div style="display: flex;justify-content: center;">
        <transition name="fade">
          <img src="../assets/coffee_worker_1.png" width="99" height="88" style="margin-bottom: -20px;margin-left: 40px;" v-show="shows[1]">
        </transition>
        <transition name="fade">
          <img src="../assets/coffee_worker_2.png" width="99" height="88" style="margin-bottom: -20px;margin-left: 40px;" v-show="shows[3]">
        </transition>
      </div>
      <div>
        <img style="width: 80%;margin-left: 10%;" src="../assets/desk.png">
      </div>
    </div>
    <div class="next-button" @click="nextStep" v-show="showNext">继续</div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      index: 0,
      showNext: false,
      shows: [false, false, false, false]
    }
  },
  methods: {
    nextStep () {
      let handlers = [() => {
        this.shows.splice(1, 1, true)
        this.index++
      }, () => {
        this.shows.splice(0, 1, false)
        setTimeout(() => {
          this.shows.splice(2, 1, true)
          this.index++
        }, 500)
      }, () => {
        this.shows.splice(1, 1, false)
        setTimeout(() => {
          this.shows.splice(3, 1, true)
          this.index++
        }, 500)
      }, () => {
        this.$emit('next')
        this.index++
      }]
      handlers[this.index]()
    }
  },
  mounted () {
    setTimeout(() => {
      this.shows.splice(0, 1, true)
      this.showNext = true
    }, 1000)
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .wrapper {
    width: 100%;
    height: 100%;
    background-image: url("../assets/work_bg.jpg");
    background-size: 100% 100%;
  }
  .next-button {
    position: absolute;
    right: 20px;
    bottom: 100px;
    text-decoration: underline;
    color: #ffffff;
  }
  .bottom {
    position: absolute;
    bottom: 0;
    width: 100%;
  }
  .fade-enter-active, .fade-leave-active {
    transition: opacity .5s;
  }
  .fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
    opacity: 0;
  }
</style>



